@import "normalize.css";
@import "./variable.css";
@import "./layout.css";
@import "./animate.css";
@import "./fonts/index.css";

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-size: 16px;
  background: linear-gradient(to bottom, #002444, #022748, #1d65a1);
  font-family: neko-icons, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: 100%;
}

ul,ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  vertical-align: middle;
}

.flex {
  display: flex;
}
.flex--center {
  justify-content: center;
  align-items: center;
}

.pointer {
  cursor: pointer;
}

