.holders {
  border: 1px solid #000;
  border-radius: 30px;
  background: #5D95D2;
  color: #ffffff;
  padding: 48px 16px;
  margin-top: 80px;

  &_title {
    font-size: 50px;
    line-height: 1;
    text-align: center;
  }

  &_content {
    max-width: 900px;
    margin: 66px auto 0;
    color: #ffffff;

    ul {
      padding-top: 14px;
      padding-bottom: 14px;
      font-size: 24px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &+ul {
        border-top: 1px solid hsla(0,0%,98%,.2);
      }
    }
  }

  &_column {
    width: 50%;
    text-align: left;
  }

  &_media {
    display: flex;
    align-items: center;
    img {
      width: 72px;
      margin-right: 16px;
    }
  }

}
