.topbar {
  padding: 20px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--primaryColor);
}

.log {
  width: 52px;
}

.link {
  border-radius: var(--radiusBold);
  letter-spacing: 0.05em;
  line-height: 1;
  padding: 6px 16px;
  cursor: pointer;
  &:hover {
    background-color: var(--primaryColor);
    color: var(--primaryBoldColor)
  }
}

.menu {
  display: flex;
  font-size: var(--primaryTextSize);
  &_item {
    @extend .link;
  }
}

.drop {
  @extend .link;
  &_text {
    font-weight: normal;
  }
  &_icon {
    margin-left: 5px;
    width: 14px;
  }
}