@font-face {
  font-family: neko-icons;
  src: url(./MAOKENZHUYUANTI-MAOKENZHUYUANTI-2.TTF) format("truetype");
  font-weight: 400;
  font-display: "auto";
  font-style: normal;
}

.neko-font {
  font-family: neko-icons !important;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: baseline;
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
