.allocation {
  width: 70vw;
  max-width: 760px;
  margin: auto;

  &_card {
    padding-top: 65px;
    padding-bottom: 52px;
    display: flex;
    align-items: center;
  }

  &_box {
    width: 194px;
    flex: none;
  }

  &_content {
    margin-left: 66px;
    color: #ffffff;
  }

  &_title {
    font-size: 50px;
    line-height: 1;
    letter-spacing: 2px;
  }

  &_text {
    margin-top: 18px;
    font-size: 16px;
    line-height: 24px;
  }
}
