.checker {
  max-width: 600px;
  width: 56vw;
  margin: 57px auto 0;
  &-title {
    font-size: 50px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 30px;
  }
}

.neko {
  &-form {
    .ant-form-item-label > label {
      font-size: 22px;
      color: #fff;
      &.ant-form-item-required:not(.ant-form-item-required-mark-optional):before {
        display: none;
      }
    }
    .ant-form-item-explain-error {
      font-size: 16px;
    }
  }
  &-btn {
    font-weight: bold;
    &.ant-btn-primary {
      background: #13e4db;
      box-shadow: none;
      &:not(:disabled):not(.ant-btn-disabled):hover {
        background: #3af0e1;
      }
    }
  }
}

.ant-select-dropdown .ant-select-item {
  padding: 15px 22px;
}
