.banner {
  width: 100%;
  min-height: 38.0625rem;
  position: relative;

  &_neko {
    width: 544px;
    position: absolute;
    right: -100px;
    top: 38px;
    z-index: 1;
  }

  &_content {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    padding-left: 60px;
    padding-top: 98px;
  }

  &_title {
    font-size: 84px;
    color: #000000;
  }

  &_text {
    max-width: 616px;
    margin-top: 30px;
    font-size: 18px;
    line-height: 24px;
  }
}

.connect {
  margin-top: 100px;
  padding: 10px 16px;
  text-align: center;
  display: inline-block;
  color: #000;
  background: #fff3a3;
  border: 1px solid #000;
  border-radius: 30px;
  cursor: pointer;
  &:hover {
    background: #ffe53b;
  }
}
